.aether__navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;

    padding: 2rem 6rem;
}
.aether__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.aether__navbar-links_logo {
  margin-right: 2rem;
}
.aether__navbar-links_logo img {
    width: 60px;
    height: 60px;
}

.aether__navbar-links_container {
    display: flex;
    flex-direction: row;
}
.aether__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.aether__navbar-links_container p,
.aether__navbar-sign p,
.aether__navbar-menu_container p{
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}
.aether__navbar-sign button,
.aether__navbar-sign_container button {
    padding: 0.5rem 1rem;
    color: #fff;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border-radius: 5px;
    border: none;
    outline: none;
    cursor: pointer;
}

.aether__navbar-menu {
    margin-left: 1rem;

    display: none;
    position: relative;
}
.aether__navbar-menu svg {
    cursor: pointer;
}
.aether__navbar-menu_container {
   display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}
.aether__navbar-menu_container p {
    margin: 1rem 0;
}
.aether__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width: 1050px) {
   .aether__navbar-links_container {
        display: none;
    }
    .aether__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
   .aether__navbar {
       padding: 2rem 4rem;
   }
}
@media screen and (max-width: 550px) {
   .aether__navbar {
       padding: 2rem;
   }
    .aether__navbar-sign {
        display: none;
    }
    .aether__navbar-menu_container {
        top: 20px
    }
    .aether__navbar-menu_container-links-sign {
        display: block;
    }
}